/*eslint-env jquery*/
import React, { Component, useState } from 'react'
import { toastr } from 'react-redux-toastr'
import MySweetAlert from '../Shared/MySweetAlert'
import Loading from '../Shared/Loading'
import { treasuresService } from '../../_services/treasures.service'
import PriceInput from '../Shared/PriceInput'
import DatePicker from '../Shared/DatePicker'
import ProvinceSelect from '../Shared/ProvinceSelect'
import Map from '../Shared/Map'
import { FaPlus } from 'react-icons/fa'
import { FiEdit } from 'react-icons/fi'
import { FiTrash2 } from 'react-icons/fi'
import { AiOutlineLoading, AiOutlineSave } from "react-icons/ai"
import config from '../../config'

// Import React Table
import matchSorter from 'match-sorter'
import ReactTable from "react-table"
import "react-table/react-table.css"
import { Input } from 'antd'
import { Tabs, Tab } from 'react-bootstrap'

import { Formik, Field, getIn, ErrorMessage } from 'formik'
import * as Yup from 'yup'
import ErrorPopUp from '../Shared/ErrorPopUp'

import ReactSelect from 'react-select'
import SimpleValue from 'react-select-simple-value'

import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import Slide from '@material-ui/core/Slide';
import DialogTitle from '@material-ui/core/DialogTitle';
import Fab from '@material-ui/core/Fab'
import { MdClose } from 'react-icons/md'

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export const Select = ({ options, value, ...props }) => (
  <SimpleValue options={options} value={value}>
    {simpleProps => <ReactSelect {...props} {...simpleProps} />}
  </SimpleValue>
);

export class DataTable extends Component {

  constructor(props, context) {
    super(props, context);

    this.state = {
      initialItems: [],
      data: [],
      modal: false,
      modalHeading: '',
      loading: true,
      confirmDeleteShow: false,
      deleteItemIndex: 0,
      activeTab: 'material',
      model: { valid_to: '', value: '', required_credit: '', state_id: '', lat: '', lng: '' }
    }

    this.getData()
  }

  handleModalCreate = () => {
    this.setState({ modalHeading: 'ثبت گنج جدید', modal: true, model: { valid_to: '', value: '', required_credit: '', state_id: '', lat: '', lng: '' } });
  }

  handleModalEdit = (e) => {
    let _model = this.state.data[e];
    this.setState({ modal: true, model: _model, modalHeading: 'ویرایش گنج ' });
  }

  handleModalHide = (e) => {
    var evt = e || window.event; // IE compatibility
    if (evt.preventDefault) {
      evt.preventDefault();
    }
    this.setState({ modal: false });
  }

  handleSweetAlertHide = () => {
    this.setState({ confirmDeleteShow: false });
  }

  getData() {
    treasuresService.getAll()
      .then((res) => {
        console.log(res.data)
        this.setState({ data: res.data.treasures, loading: false, isSubmitting: false, modal: false });
      })
      .catch((error) => {
        this.handleError(error)
      });
  };


  handleError(error) {
    this.setState({ loading: false });
    // // toastr.warning('مشکلی پیش آمد', error);
  }

  handleDelete = (e) => {
    let _model = this.state.data[e];

    this.setState({ confirmDeleteShow: true, model: _model });
  }

  handleConfirmDelete = () => {
    this.setState({ loading: true, confirmDeleteShow: false });

    treasuresService.delete(this.state.model.id)
      .then((res) => {
        toastr.success('حذف گنج', 'گنج با موفقیت حذف شد');
        this.getData();
      })
      .catch((error) => {
        this.handleError(error)
      });
  }

  handleSubmit = (values) => {
    this.setState({ loading: true });
    // console.log(JSON.stringify(values));


    if (values.id) {

      treasuresService.edit(values.id, JSON.stringify(values))
        .then((res) => {

          toastr.success('ویرایش گنج', 'گنج جدید با موفقیت ویرایش شد');
          this.setState({ modal: false });
          this.getData();

        })
        .catch((error) => {
          this.handleError(error)
        });

    } else {
      console.log(JSON.stringify(values));
      treasuresService.add(JSON.stringify(values))
        .then((res) => {

          toastr.success('ثبت گنج جدید', 'گنج جدید با موفقیت ثبت شد');
          this.setState({ modal: false });
          this.getData();

        })
        .catch((error) => {
          this.handleError(error)
        });

    }
  }

  render() {

    const ValidationSchema = Yup.object().shape({
      state_id: Yup.string().nullable().trim().required('استان'),
      valid_to: Yup.string().nullable().trim().required('تاریخ اعتبار گنج'),
      value: Yup.string().nullable().trim().required('مبلغ گنج'),
      required_credit: Yup.string().nullable().trim().required('حداقل میزان شارژ'),
      lat: Yup.string().nullable().trim().required('مختصات'),
    });
    const { model, loading, dialogTitle } = this.state;
    return (
      <div>

        <div className="row table-container" style={{ marginTop: 0 }}>
          <ReactTable
            data={this.state.data}
            filterable
            loading={loading}
            previousText='صفحه قبل'
            nextText='صفحه بعد'
            loadingText='در حال بارگذاری ...'
            noDataText='نتیجه ای یافت نشد'
            pageText='صفحه'
            ofText='از'
            rowsText='تعداد نمایش'

            defaultFilterMethod={(filter, row) =>
              String(row[filter.id]) === filter.value}
            columns={[
              {
                Header: "ردیف",
                id: "row",
                maxWidth: 50,
                filterable: false,
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                className: "numberFa",
                Cell: (row) => {
                  return row.index + 1;
                }
              },
              {
                Header: "استان",
                accessor: "state",
                id: "state",
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                filterMethod: (filter, rows) =>
                  matchSorter(rows, filter.value, { keys: ["state"], threshold: matchSorter.rankings.CONTAINS }),
                filterAll: true,
                Filter: ({ onChange }) =>
                  <Input placeholder="" allowClear onChange={event => onChange(event.target.value)} />
              },
              {
                Header: "مبلغ گنج",
                accessor: "value",
                id: "value",
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                filterMethod: (filter, rows) =>
                  matchSorter(rows, filter.value, { keys: ["value"], threshold: matchSorter.rankings.CONTAINS }),
                filterAll: true,
                Filter: ({ onChange }) =>
                  <Input placeholder="" allowClear onChange={event => onChange(event.target.value)} />
              },
              {
                Header: "تاریخ اعتبار",
                accessor: "valid_to_shamsi",
                id: "valid_to_shamsi",
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                filterMethod: (filter, rows) =>
                  matchSorter(rows, filter.value, { keys: ["valid_to_shamsi"], threshold: matchSorter.rankings.CONTAINS }),
                filterAll: true,
                Filter: ({ onChange }) =>
                  <Input placeholder="" allowClear onChange={event => onChange(event.target.value)} />
              },
              {
                Header: "حداقل میزان شارژ",
                accessor: "required_credit",
                id: "required_credit",
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                filterMethod: (filter, rows) =>
                  matchSorter(rows, filter.value, { keys: ["required_credit"], threshold: matchSorter.rankings.CONTAINS }),
                filterAll: true,
                Filter: ({ onChange }) =>
                  <Input placeholder="" allowClear onChange={event => onChange(event.target.value)} />
              },
              {
                Header: "ویرایش",
                accessor: 'id',
                style: { display: 'flex', alignItems: 'center', justifyContent: 'center' },
                sortable: false,
                // filterable: false,
                Filter: ({ filter, onChange }) =>
                  <a onClick={this.handleModalCreate} className="btn btn-primary react-table-add-btn" >
                    <span>
                      <FaPlus size="16px" />
                      افزودن گنج جدید
                    </span>
                  </a>,
                Cell: row => (
                  <div>
                    <a onClick={() => this.handleModalEdit(row.index)} data-index={row.index} className="btn btn-sm btn-simple btn-success bmd-ripple bmd-flat-btn" rel="tooltip" data-original-title="ویرایش"><FiEdit size="24px" /></a>

                    <a onClick={() => this.handleDelete(row.index)} data-index={row.index} className="btn btn-sm btn-simple btn-danger bmd-ripple bmd-flat-btn delete-btn" data-original-title="حذف" rel="tooltip"><FiTrash2 size="24px" /></a>
                  </div>
                )
              }
            ]}
            defaultPageSize={10}
            className="-striped -highlight"
          />

        </div>

        <Dialog
          open={this.state.modal}
          onClose={this.handleModalHide}
          TransitionComponent={Transition}
          scroll={'paper'}
          fullScreen={true}
          maxWidth="sm"
          aria-labelledby="scroll-dialog-title"
          aria-describedby="scroll-dialog-description">
          <DialogTitle>
            <Fab color="secondary" aria-label="refresh" size="small" onClick={() => this.handleModalHide()} className="modal-close-btn">
              <MdClose size="20px" />
            </Fab>
            <span className="dialogTitle">{dialogTitle}</span>
            <span style={{ marginRight: 10 }}></span>
          </DialogTitle>
          <DialogContent dividers={true}
            style={{ overflowY: 'visible', minHeight: 225 }}
          >

            <Formik
              initialValues={model}
              onSubmit={(values, { setSubmitting }) => {
                // // console.log(values);
                this.handleSubmit(values)
              }}
              validationSchema={ValidationSchema}
            >
              {props => {
                const {
                  values,
                  touched,
                  errors,
                  isSubmitting,
                  handleChange,
                  handleBlur,
                  setFieldValue,
                  handleSubmit,
                } = props;
                return (

                  <div className="row" style={{ position: "relative" }}>
                    <div className="col-md-12">
                      <form onSubmit={handleSubmit}>

                        <div className="row">
                          <div className="form-inline">

                            <div className="col-md-4 col-sm-12">
                              <label>استان</label>
                              <ProvinceSelect
                                placeholder="انتخاب استان"
                                defaultValue={values.state_id}
                                onChange={(val) => {
                                  setFieldValue("state_id", val.value)
                                }}
                              />
                              <ErrorMessage component={ErrorPopUp} name="state_id" />
                            </div>

                            <div className="col-md-4 col-sm-12">
                              <label>مبلغ گنج</label>
                              <PriceInput
                                defaultValue={values.value}
                                onChange={val => {
                                  setFieldValue("value", val)
                                }}
                                className={
                                  getIn(errors, "value") &&
                                    getIn(touched, "value")
                                    ? "text-input has-error rtl"
                                    : "text-input required rtl"
                                }
                              />
                              <ErrorMessage component={ErrorPopUp} name="value" />
                            </div>

                            <div className="col-md-4 col-sm-12">
                              <label>حداقل میزان شارژ</label>
                              <PriceInput
                                defaultValue={values.required_credit}
                                onChange={val => {
                                  setFieldValue("required_credit", val)
                                }}
                                className={
                                  getIn(errors, "required_credit") &&
                                    getIn(touched, "required_credit")
                                    ? "text-input has-error rtl"
                                    : "text-input required rtl"
                                }
                              />
                              <ErrorMessage component={ErrorPopUp} name="required_credit" />
                            </div>

                            <div className="col-md-4 col-sm-12">
                              <label>معتبر تا تاریخ</label>
                              <DatePicker
                                defaultValue={values.valid_to}
                                onChange={val => {
                                  setFieldValue("valid_to", val)
                                }}
                                className={
                                  getIn(errors, "name") &&
                                    getIn(touched, "name")
                                    ? "text-input has-error ltr"
                                    : "text-input required ltr"
                                }
                              />
                              <ErrorMessage component={ErrorPopUp} name="valid_to" />
                            </div>

                            <div className="clearfix form-row"></div>

                            <div className="col-md-12">
                              <label></label>
                              <Map zoom={12}
                                onChange={(val) => {
                                  setFieldValue('lat', val.lat.toFixed(6));
                                  setFieldValue('lng', val.lng.toFixed(6));
                                }}
                                center={values.lat && values.lng ? {
                                  lat: values.lat > 0 ? values.lat : 38.079987,
                                  lng: values.lng > 0 ? values.lng : 46.296130
                                }
                                  : { lat: 38.079987, lng: 46.296130 }}
                              />
                              {
                                (errors.lat || errors.lng) &&
                                <div className="required_validator visible"><div className="validator_container"><span>{'وارد کردن [مختصات محل] الزامی است'}</span></div></div>
                              }
                            </div>

                            <div className="clearfix form-row"></div>

                            <div className="col-md-12">
                              <button type="submit" className={"icon button submit-btn " + (isSubmitting ? "isSubmitting" : "")} disabled={isSubmitting}>
                                {isSubmitting ?
                                  <AiOutlineLoading className="mdi-spin" size="22px" />
                                  :
                                  <AiOutlineSave size="22px" />
                                }ذخیره</button>
                            </div>
                            <div className="clearfix form-row"></div>

                          </div>
                        </div>


                      </form>
                    </div>
                  </div>

                );
              }}
            </Formik>

          </DialogContent>
        </Dialog >

        <MySweetAlert
          show={this.state.confirmDeleteShow}
          onCancel={this.handleSweetAlertHide}
          onConfirm={this.handleConfirmDelete}
          title={"آیا از حذف گنج مطمئن هستید؟"}
        >
        </MySweetAlert>

      </div >
    );
  }
}


const Treasures = props => {
  const [activeTab, setActiveTab] = useState('treasures')

  return (

    <Tabs transition={false}
      id="controlled-tab-example"
      activeKey={activeTab}
      onSelect={activeTab => setActiveTab(activeTab)}
    >
      <Tab eventKey="treasures" title="جوایز">
        {activeTab === 'treasures' ?
          <DataTable giftType={0} />
          : ''}
      </Tab>

    </Tabs>
  )
}
export default Treasures;